@import "https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap";
:root {
  --bg-color-main: #ececec;
  --color-main: #3e3e3e;
  --color-secondary: #656565;
  --color-white: #fff --color-black: #000;
}

.page-header {
  width: 100vw;
  height: 11.875rem;
  background-image: url("Rectangle-53.ac23ad4b.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.page-header__navbar {
  width: 100%;
  height: 3.75rem;
  background-color: var(--bg-color-main);
  justify-content: center;
  align-items: center;
  display: flex;
}

.page-header__logo {
  width: 3.125rem;
  height: 1.875rem;
  fill: var(--color-main);
  stroke: var(--color-main);
}

.profile-container {
  width: 18.75rem;
  height: calc(100vh - 15.625rem);
  background-color: var(--color-white);
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 1px 0 10px -1px #000;
}

.profile__img-container {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  margin-top: 1.875rem;
  position: relative;
  overflow: hidden;
}

.profile__img {
  width: 11rem;
  height: auto;
  object-fit: contain;
  position: absolute;
  right: -15px;
}

.profile__name-box, .profile__contact-box {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.name {
  color: var(--color-main);
  margin-top: 1.875rem;
  font-size: 24px;
  font-weight: 500;
  line-height: 21.6px;
}

.job {
  color: var(--color-secondary);
  margin-top: .625rem;
  font-size: 16px;
  font-weight: 500;
  line-height: 14.4px;
}

.profile__contact-box {
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-top: 3.75rem;
  display: flex;
}

.contact__link {
  align-items: center;
  gap: 5px;
  text-decoration: none;
  display: flex;
}

.contact__text {
  color: var(--color-secondary);
  font-size: 1rem;
  font-weight: 500;
  line-height: 14.4px;
}

.logo {
  height: 1.5rem;
  width: 1.5rem;
  fill: var(--color-secondary);
  stroke: var(--color-secondary);
}

.location {
  color: var(--color-secondary);
  font-size: 14px;
  font-weight: 500;
  line-height: 12.6px;
  position: absolute;
  bottom: 1.25rem;
}

* {
  box-sizing: border-box;
  font-optical-sizing: auto;
  margin: 0;
  font-family: Inter, sans-serif;
  font-style: normal;
}

